class ClienteInternet {
  constructor({id = 0, nombre, apellidoPaterno, apellidoMaterno, email, activo, cliente, idCliente, esNuevo,
                hijos = [], photoURL, telefono, nombreCompleto}) {
    this.id = id
    this.nombre = nombre,
    this.apellidoPaterno = apellidoPaterno || null,
    this.apellidoMaterno = apellidoMaterno || null,
    this.email = email,
    this.hijos = hijos || [],
    this.activo = activo,
    this.cliente = cliente,
    this.idCliente = idCliente,
    this.esNuevo = esNuevo,
    this.photoURL = photoURL,
    this.telefono = telefono,
    this.nombreCompleto = nombreCompleto
    this.tipo = 'padre_de_familia'
    this.esPadreDeFamilia = true
  }
}

export default ClienteInternet;
