<template>
  <v-container :class=" $vuetify.breakpoint.smAndDown ? 'mt-16 pt-16' : 'mt-8' ">
    <v-row>
      <v-col cols="12" class="text-center">
        <img :src="require('./../assets/hapit_logo.svg')" :width="$vuetify.breakpoint.smAndDown ? '300' : '350'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h1 class="font-weight-bold text--disabled">Inicie sesión</h1>
      </v-col>
      <v-col cols="12" class="text-center">

        <v-tabs centered fixed-tabs>
          <v-tab>
            Soy padre de DAnis
          </v-tab>
          <v-tab>
            Soy alumno
          </v-tab>
          <v-tab>
            Soy cafetería
          </v-tab>

          <v-tab-item class="text-center" style="z-index: 5;">
            <v-card flat class="text-center mt-4 transparent">
              <v-card-text>
                <v-row>
                  <v-col cols="12" class="text-center">
                    <h3 class="text--disabled">Elija una de las siguientes opciones:</h3>
                  </v-col>
                  <v-col cols="12" class="text-center" style="display: grid; place-content: center;">
                    <v-bottom-navigation class="ma-0 pa-0 text-center">
                      <v-btn x-small icon @click="loginMicrosoft">
                        <small class="indigo--text caption">Microsoft</small>
                        <v-icon class="indigo--text">mdi-microsoft</v-icon>
                      </v-btn>
                      <v-btn x-small icon @click="loginGoogle">
                        <small class="red--text caption">Google</small>
                        <v-icon class="red--text">mdi-google</v-icon>
                      </v-btn>
                      <!-- BOTÓN OCULTO TEMPORALMENTE
                      <v-btn x-small icon @click="loginFacebook">
                        <small class="blue&#45;&#45;text text&#45;&#45;darken-4 caption">Facebook</small>
                        <v-icon class="blue&#45;&#45;text text&#45;&#45;darken-4">mdi-facebook</v-icon>
                      </v-btn>-->
                    </v-bottom-navigation>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item class="text-center" style="display: grid;place-content: center; z-index: 5">
            <v-card flat width="400" class="mx-3 text-center mt-4">
              <v-row>
                <v-col cols="12" class="text-center">
                  <h3 class="text--disabled">Ingrese con su matrícula o código QR</h3>
                </v-col>
                <v-col cols="12" class="text-center" style="display: grid; place-content: center;">
                  <v-bottom-navigation class="ma-0 pa-0 text-center">
                    <v-btn x-small icon @click="dialogoMatricula = true">
                      <small class="primary--text caption">Matrícula</small>
                      <v-icon class="primary--text">mdi-alphabetical</v-icon>
                    </v-btn>
                    <v-btn x-small icon>
                      <small class="primary--text caption">Código QR</small>
                      <v-icon class="primary--text">mdi-qrcode-scan</v-icon>
                    </v-btn>
                  </v-bottom-navigation>
                </v-col>
              </v-row>
            </v-card>
          </v-tab-item>
          <v-tab-item class="text-center" style="display: grid;place-content: center; z-index: 5">
            <v-card flat width="400" class="mx-3 text-center mt-4">
              <v-form ref="formLoginCafeteria" lazy-validation>
                <v-row>
                  <v-col cols="12" class="text-center">
                    <v-text-field v-model="usuarioCafeteria.email" label="Usuario" :rules="textFieldRules"
                                  @keyup.enter="loginCafeteria"/>
                  </v-col>
                  <v-col cols="12" class="text-center">
                    <v-text-field v-model="usuarioCafeteria.password" label="Contraseña" type="password"
                                  :rules="textFieldRules" @keyup.enter="loginCafeteria" />
                  </v-col>
                  <v-col cols="12" class="text-center">
                    <v-btn rounded color="primary" @click="loginCafeteria">Iniciar sesión</v-btn>
                  </v-col>
                </v-row>
              </v-form>
            </v-card>
          </v-tab-item>
        </v-tabs>
      </v-col>
      <v-col cols="12">
          <v-card flat class="text-center transparent">
            <img :src="require('./../assets/login_personajes.svg')" :width="$vuetify.breakpoint.smAndDown ? '350' : '500'"/>
          </v-card>
      </v-col>
    </v-row>
    <dialogo v-model="dialogoMatricula" title="Ingrese su matrícula">
      <v-row>
        <v-col cols="12">
          <v-text-field v-model="matriculaAlumno" label="Matrícula" autofocus @keyup.enter="loginAlumnoMatricula"/>
        </v-col>
        <v-col cols="12" class="text-center">
          <v-btn text color="primary" rounded @click="loginAlumnoMatricula">
            Iniciar sesión
          </v-btn>
        </v-col>
      </v-row>
    </dialogo>
  </v-container>
</template>

<script>
  import AuthConst from '../config/AuthConst'
  import firebase from '../config/Firebase'
  import ClientesInternetService from '../services/ClientesInternetService'
  import AlumnosService from "../services/AlumnosService";
  import ClienteInternet from "../models/ClienteInternet";
  import EncargadosService from "@/services/EncargadosService";

  export default {
        name: "Login",
      data() {
          return{
            matriculaAlumno: null,
            usuarioCafeteria: {},
            dialogoMatricula: false,
            textFieldRules: [v => !!v || 'Este campo es requerido']
          }
      },
      async created() {
        let vm = this
        vm.$loader = true

        try {
          let result = await firebase.getAuthResult()

          let {user} = result
          if (user) {
            let clienteInternet = new ClienteInternet(
              {...user, ...await ClientesInternetService.searchClienteInternet(user), tipo: 'padre_de_familia', esPadreDeFamilia: true}
              )
            localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(clienteInternet))
            vm.$usuario = clienteInternet

            vm.$router.push({ name: 'Dashboard' })
          } else {
            vm.$loader = false
          }
        } catch (e) {
          if (e.code === 'auth/timeout') {
            vm.$showAlerta('600', 'warning' , 'mdi-check-circle-outline', 'Se ha agotado el tiempo de espera, verifique su conexión de internet e inténtelo nuevamente.', true )
          }
        }

        vm.$loader = false
        },
      methods: {
        loginGoogle() {
          firebase.googleSignIn()
        },
        loginFacebook() {
          firebase.facebookSignIn()
        },
        loginTwitter() {
          firebase.twitterSignIn()
        },
        loginMicrosoft() {
          firebase.microsoftSignIn()
        },
        loginAlumnoMatricula () {
          const vm = this
          vm.$loader = true
          AlumnosService.busquedaPorMatricula(vm.matriculaAlumno)
            .then(alumno => {
              let usuarioAlumno = { ...alumno, tipo: 'alumno', esAlumno: true } // Valores para marcar usuario como alumno
              localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(usuarioAlumno))
              vm.$usuario = usuarioAlumno

              vm.$router.push({ name: 'Dashboard' })
            })
            .catch(() => (vm.$showAlerta('600', 'error', 'mdi-alert-circle',
              'No fue posible consultar el perfil del alumno.', true)))
            .finally(() => (vm.$loader = false))
        },
        loginCafeteria () {
          if (this.$refs.formLoginCafeteria.validate()) {
            const vm = this
            vm.$loader = true

            EncargadosService.login(vm.usuarioCafeteria)
              .then(encargado => {
                let usuarioEncargado = { ...encargado, tipo: 'encargado', esEncargado: true } // Valores para marcar usuario como encargado
                localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(usuarioEncargado))
                vm.$usuario = usuarioEncargado

                vm.$router.push({ name: 'Dashboard' })
              })
              .catch((e) => {
                const {status} = JSON.parse(JSON.stringify(e))

                if (status === 404)
                  vm.$showAlerta('600', 'warning', 'mdi-alert-circle',
                    'Este usuario no está registrado, comuníquese con el administrador y vuelva a intentarlo más tarde.',
                    true)
                else vm.$showAlerta('600', 'error', 'mdi-alert-circle',
                  'No fue posible consultar el perfil de cafetería.', true)
              })
              .finally(() => (vm.$loader = false))
          }
        }
      }
    }
</script>

<style scoped>
  .botones{
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent!important;
    elevation: 0;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.2), 0px 0px 0px 0px rgba(0, 0, 0, 0.14), 0px 0px 0px 0px rgba(0, 0, 0, 0.12) !important;
  }
  .v-item-group.v-bottom-navigation {
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0) !important;
    justify-content: end;
  }
</style>
